                  v-else
                >
                  {{ (total_price * (Discount / 100) + shipping) | currency }}
                </p>
                <a
                  href="#collapseOne"
                  class="btn btn-link ml-md-auto ml-5 text-earthy"
                  data-toggle="collapse"
                  data-target="#collapseOne"
                  aria-expanded="true"
                  aria-controls="collapseOne"
                >
                   <i class="fas fa-caret-down"></i>
                </a>
              </div>
            </div>
          </div>

          <div
            id="collapseOne"
            class="collapse"
            aria-labelledby="headingOne"
            data-parent="#accordionExample"
          >
            <table class="table bg-transparent rwd-table">
              <thead>
                <tr>
                  <th class="text-center" width="100"></th>
                  <th class="text-center" width="150" colspan="10"></th>
                  <th class="text-center" width="100"></th>
                  <th class="text-center" width="100"></th>
                  <th class="text-center" width="80" colspan="5"></th>
                </tr>
              </thead>
              <tbody v-for="item in custcart" :key="item.id">
                <tr>
                  <td class="align-middle text-center" data-th="">
                    <img
                      :src="item.product.imageUrl"
                      :alt="item.product.title"
                      class="img-thumbnail"
                    />
                  </td>
                  <td class="align-middle text-center" colspan="10" data-th="">
                    {{ item.product.title }}
                  </td>
                  <td class="align-middle text-center" data-th="">{{ item.qty }}</td>
                  <td class="align-middle text-center" v-if="!item.product.price" data-th="">
                    {{ item.product.origin_price }}
                  </td>
                  <td class="align-middle text-center" data-th="" v-else>
                    {{ item.product.price }}
                  </td>
                  <td
                    class="align-middle text-center"
                    colspan="5"
                    v-if="!item.product.price"
                    data-th=""
                  >
                    {{ item.product.origin_price * item.qty }}
                  </td>
                  <td class="align-middle text-center" colspan="5" data-th="" v-else>
                    {{ item.product.price * item.qty }}
                  </td>
                </tr>
              </tbody>
            </table>
            <div class="text-right">
              <p>{{ total_price | currency }}</p>
              <p>
                
                <span :class="{ 'text-danger': Discount !== 100 }" v-if="Discount !== 100"
                  >{{ 100 - Discount }}%OFF</span
                >
                <span :class="{ 'text-danger': Discount !== 100 }" v-else></span>
              </p>
              <p>{{ shipping | currency }}</p>
              <p class="h3" :class="{ 'text-maincolor': Discount !== 100 }" v-if="Discount !== 100">
                
                {{ (total_price * (Discount / 100) + shipping) | currency }}
              </p>
              <p class="h3" v-else>
                
                {{ (total_price + shipping) | currency }}
              </p>
            </div>
          </div>
          <div class="container hpx-10 mt-3">
            <div class="row">
              <div class="col-md-6">
                <p class="text-danger"> goodfood9999  3 </p>
              </div>
              <div class="col-md-6">
                <div class="input-group mb-3">
                  <input
                    type="text"
                    class="form-control"
                    placeholder=""
                    aria-label=""
                    aria-describedby="button-addon2"
                    v-model="couponcode"
                  />
                  <div class="input-group-append">
                    <button
                      type="button"
                      class="btn btn-outline-secondary"
                      id="button-addon2"
                      @click="addcoupon"
                    >
                      
                    </button>
                  </div>
                </div>
                <Alertinfotext />
              </div>
            </div>
          </div>

          <h5 class="mt-5 text-center py-3 mb-2 bg-light"></h5>
          <validation-observer v-slot="{ invalid }">
            <form class="needs-validation" @submit.prevent="subOrder">
              <div class="form-row">
                <validation-provider
                  class="col-md-4 mb-3"
                  rules="required|zhtw_name"
                  v-slot="{ errors, classes }"
                >
                  <div class="form-group">
                    <!--  -->
                    <label for="username"></label>
                    <input
                      type="text"
                      name=""
                      :class="classes"
                      class="form-control"
                      id="username"
                      placeholder=""
                      v-model="form.user.name"
                    />
                    <!--  -->
                    <span class="invalid-feedback">{{ errors[0] }}</span>
                  </div>
                </validation-provider>
                <validation-provider
                  class="col-md-4 mb-3"
                  rules="required|numeric"
                  v-slot="{ errors, classes }"
                >
                  <div class="form-group">
                    <label for="usertel"></label>
                    <input
                      type="text"
                      name=""
                      class="form-control"
                      :class="classes"
                      id="usertel"
                      placeholder=""
                      v-model="form.user.tel"
                    />
                    <span class="invalid-feedback">{{ errors[0] }}</span>
                  </div>
                </validation-provider>
                <validation-provider
                  class="col-md-4 mb-3"
                  rules="required|email"
                  v-slot="{ errors, classes }"
                >
                  <div class="form-group">
                    <label for="usermail">Email</label>
                    <input
                      type="text"
                      name="email"
                      class="form-control"
                      :class="classes"
                      id="usermail"
                      placeholder="example@email.com"
                      v-model="form.user.email"
                    />
                    <span class="invalid-feedback">{{ errors[0] }}</span>
                  </div>
                </validation-provider>
                <div class="form-group col-md-4">
                  <label for="inputState"></label>
                  <select
                    id="inputState"
                    name="country"
                    class="form-control"
                    v-model="form.user.city"
                  >
                    <option value=""></option>
                    <option v-for="(item, index) in countorys" :key="index">
                      {{ item }}
                    </option>
                  </select>
                </div>
                <validation-provider
                  class="col-md-8 mb-3"
                  rules="required"
                  v-slot="{ errors, classes }"
                >
                  <div class="form-group">
                    <label for="useraddress"></label>
                    <input
                      type="text"
                      name=""
                      class="form-control"
                      :class="classes"
                      id="useraddress"
                      placeholder=""
                      v-model="form.user.address"
                    />
                    <span class="invalid-feedback">{{ errors[0] }}</span>
                  </div>
                </validation-provider>
                <div class="col-md-12 mb-3">
                  <label for="validationTextarea"></label>
                  <textarea
                    class="form-control resize-none"
                    id="validationTextarea"
                    placeholder="()"
                    v-model="form.message"
                  ></textarea>
                </div>
              </div>
              <button class="btn btn-maincolor float-right" type="submit" :disabled="invalid">
                
              </button>
            </form>
          </validation-observer>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Alertinfotext from '@/components/kit/Alert_infotext.vue'

export default {
  name: 'CustomInfo',
  data () {
    return {
      countorys: [
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        ''
      ],
      custcart: {},
      form: {
        user: {
          name: '',
          email: '',
          tel: '',
          city: '',
          address: ''
        },
        message: ''
      },
      custdata: JSON.parse(localStorage.getItem('custinfo')) || [],
      totalPricePack: [],
      finalPricepack: [],
      couponcode: '',
      isCoupon: false,
      isLoading: false,
      Discount: 100,
      total_price: 0,
      shipping: 0
    }
  },
  created () {
    this.getcart()
    this.getcustinfo()
  },
  components: {
    Alertinfotext
  },
  methods: {
    getcart () {
      const vm = this
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`
      vm.isLoading = true
      vm.$http
        .get(api)
        .then((resp) => {
          vm.custcart = resp.data.data.carts
        })
        .then(() => {
          vm.totalPricecal()
          vm.ShippingFee()
          vm.isLoading = false
        })
    },
    getcustinfo () {
      const vm = this
      vm.custdata.forEach((item) => {
        vm.form.user.name = item.user.name
        vm.form.user.email = item.user.email
        vm.form.user.tel = item.user.tel
        vm.form.user.city = item.user.city
        vm.form.user.address = item.user.address
        vm.form.message = item.message
      })
    },
    addcoupon () {
      const vm = this
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/coupon`
      const coupon = {
        code: vm.couponcode
      }
      vm.$http.post(api, { data: coupon }).then((response) => {
        if (response.data.success) {
          vm.$infotext.$emit('messsage:push', '', 'success', 'yes')
        } else {
          vm.$infotext.$emit('messsage:push', '', 'danger', 'no')
        }
        vm.getcart()
        vm.couponcode = ''
      })
    },
    totalPricecal () {
      const vm = this
      vm.totalPricePack = []
      vm.custcart.forEach((item) => {
        if (item.coupon) {
          vm.isCoupon = true
          vm.Discount = item.coupon.percent
        }
        if (item.product.price) {
          vm.totalPricePack.push(item.product.price * item.qty)
        } else {
          vm.totalPricePack.push(item.product.origin_price * item.qty)
        }
      })
      vm.total_price = vm.totalPricePack.reduce((a, b) => a + b, 0)
    },
    ShippingFee () {
      const vm = this
      if (vm.total_price < 3000) {
        vm.shipping = 60
      } else {
        vm.shipping = 0
      }
      vm.isLoading = false
    },
    subOrder () {
      const vm = this
      vm.isLoading = true
      vm.custdata.splice(0, 1)
      vm.custdata.push(vm.form)
      localStorage.setItem('custinfo', JSON.stringify(vm.custdata))
      vm.$router.push('/checkpage/custcheckout')
      vm.isLoading = false
    }
  }
}
</script>
